<template>
  <section>
    <div class="navbar-container">
      <div class="container-fluid navbar-header flex">
        <span class="page-title">裸金属监控</span>
      </div>
    </div>
    <div class="navbar-container">
       <div class="container-fluid">
          <hr class="normal"/>
          <div class="navbar-header flex clearfix">
            <el-select placeholder="请选择VDC" v-model="currentApp" value-key="id">
              <el-option v-for="item in app_list" :key="item.id" :label="item.name" :value="item"></el-option>
            </el-select>
            <el-input placeholder="请输入IP地址" style="width: 300px;" v-model="ip_string">
                  <el-button slot="append" @click="FnGetBmsList"><i class="el-icon-search"></i></el-button>
            </el-input>
          </div>
       </div>
    </div>
    <div class="navbar-container box-shadow">
      <div class="container-fluid" style="padding-bottom:10px;">
        <p>下面列表只显示开通监控权限的裸金属：</p>
         <div class="border" style="padding: 10px 20px;">
            <el-radio-group class="purple-color" v-model="currentBms">
              <el-radio v-for="item in bms_list" :key="item.id" :label="item">
                <span v-text="item.name"></span>
              </el-radio>
            </el-radio-group>
            <p v-if="bms_list.length <= 0" style="text-align:center;">暂无数据！</p>
          </div>
          <p class="m-top20"  v-if="currentBms"><el-button class="cds-button" type="primary" @click="FnCheckMonitorData">查看监控数据</el-button></p>
      </div>
    </div>
    <div class="m-top20" v-if="searchStatus">
      <div class="card-form" v-if="currentBms">
        <p><label>编号：</label><span>{{currentBms.id}}</span></p>
        <p><label>所属虚拟数据中心：</label><span>{{currentBms.appName}}</span><label class="m-left30">区域：</label><span>{{currentBms.regionName}}</span></p>
      </div>
      <div class="card-form">
        <el-radio-group class="purple-color" v-model="monitor_type_default">
          <el-radio-button v-for="(m, i) in monitor_type_list" :key="i" :label="m">
            <span v-text="m.text"></span>
          </el-radio-button>
        </el-radio-group>
      </div>
      <div class="card-form">
        <el-radio-group class="purple-color" v-model="sel_time">
          <el-radio-button v-for="(m, i) in time_list" :key="i" :label="m.value">
            <span v-text="m.text"></span>
          </el-radio-button>
        </el-radio-group>
      </div>
      <div v-if="monitor_type_default">
        <div class="card-form" v-for="(item, i) in monitor_type_default.tab_list" :key="i">
          <div class="card-article">
            <span>{{item.title}}</span>
            <el-radio-group class="purple-color m-left30" v-model="item.default">
              <el-radio-button v-for="(m, l) in item.data_list" :key="l" :label="m" :value="m">
                <span v-text="m.volumePath" v-if="item.type == 'disk'"></span>
                <span v-text="m.ipAddress" v-if="item.type == 'nic'"></span>
              </el-radio-button>
            </el-radio-group>
          </div>
          <div :class="{grid: monitor_type_default.type == 'source'}">
            <div class="card border background-fff" v-for="(m, j) in item.list" :key="j">
              <div v-if="m.loading_stauts" style="display: inline-block;"><i class="el-icon-loading"></i> <span>数据正在加载中</span></div>
              <span v-if="!m.loading_stauts && m.nomoreData_stauts">暂无数据！</span>
              <div v-if="!m.loading_stauts && !m.nomoreData_stauts && m.value != 'disk_used'" class="chart-container" :id="m.value"></div>
              <table v-if="!m.loading_stauts && !m.nomoreData_stauts && m.value == 'disk_used'" class="table">
                  <thead>
                    <tr><th>容量</th><th>可用</th><th>挂载点</th></tr>
                  </thead>
                  <tbody>
                    <tr v-for="(n, k) in diskUsedData" :key="k">
                      <td v-text="n.fileSystemSize">39G</td><td v-text="n.fileSystemFree">G</td><td v-text="n.mountpoint">/</td>
                    </tr>
                  </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import {getAppList, getBmsList, getMonitorData, getBmsInfo} from '@/service/monitor/bms_monitor';
import {Message} from 'element-ui';
export default {
    data () {
      return {
        time_list: [
          {value: 'fifteen_minutes', text: '15分钟'},
          {value: 'one_hour', text: '1小时'},
          {value: 'one_day', text: '1天'}
        ],
        sel_time: '',
        monitor_type_list: [
          {
            type: 'source',
            text: '资源监控',
            tab_list: [
              {
                title: 'CPU/内存',
                type: 'cpu/ram',
                list: [
                  {
                    value: 'cpu_used',
                    loading_stauts: false,
                    nomoreData_stauts: false
                  },
                  {
                    value: 'mem_used',
                    loading_stauts: false,
                    nomoreData_stauts: false
                  },
                ]
              },
              {
                title: '磁盘监控指标',
                type: 'disk',
                list: [
                  {
                    value: 'disk_used',
                    loading_stauts: false,
                    nomoreData_stauts: false
                  },
                  {
                    value: 'disk_iops',
                    loading_stauts: false,
                    nomoreData_stauts: false
                  },
                  {
                    value: 'disk_throughput',
                    loading_stauts: false,
                    nomoreData_stauts: false
                  }
                ],
                data_list: [],
                default: null
              },
              {
                title: '网络监控指标',
                type: 'nic',
                list: [
                  {
                    value: 'network_throughput',
                    loading_stauts: false,
                    nomoreData_stauts: false
                  }
                ],
                data_list: [],
                default: null
              }
            ]
          },
          {
            type: 'device',
            text: '设备监控',
            tab_list: [
              {
               title: 'CPU温度',
               type: 'cpu',
               list: [
                  {
                    value: 'cpu_temperature',
                    loading_stauts: false,
                    nomoreData_stauts: false
                  }
               ]
              },
              {
                title: '风扇转速',
                type: 'fan',
                list: [
                  {
                    value: 'fan',
                    loading_stauts: false,
                    nomoreData_stauts: false
                  }
                ]
              }
            ]
          }
        ],
        monitor_type_default: null,
        app_list: [],
        currentApp: null,
        ip_string: '',
        bms_list: [],
        currentBms: null,
        loading_stauts: false,
        nomore_stauts: false,
        disk_list: [],
        diskDefault: null,
        nic_list: [],
        nicDefault: null,
        searchStatus: false,
        diskUsedData: []
      }
    },
    methods: {
        async FnGetAppList () {
          let res = await getAppList()
          if (res.code === 'Success') {
              this.app_list = res.data;
          } else {
              Message({message: res.msg, type: 'error'});
          }
        },
        async FnGetBmsList () {
          this.bms_list = [];
          this.currentBms = null;
          let reqData = {
            appId: this.currentApp.id,
            ipString: this.ip_string,
            current: 1,
            size: 10000
          }
          let res = await getBmsList (reqData);
          if (res.code === 'Success') {
            let data = res.data ? res.data.records : [];
            this.bms_list
            data.forEach(item => {
              if (item.enableMonitor) {
                this.bms_list.push(item)
              }
            })
          } else {
            Message({message: res.msg, type: 'error'});
          }
        },
        async FnCheckMonitorData () {
          let res = await getBmsInfo(this.currentBms.id);
          if (res.code === 'Success') {
            this.monitor_type_list[0].tab_list[1].data_list = res.data ? res.data.volumes : [];
            this.monitor_type_list[0].tab_list[1].default = this.monitor_type_list[0].tab_list[1].data_list[0];
            this.monitor_type_list[0].tab_list[2].data_list = res.data ? res.data.networks.networkCards : [];
            this.monitor_type_list[0].tab_list[2].default = this.monitor_type_list[0].tab_list[2].data_list[0];

            this.searchStatus = true;
            this.monitor_type_default = this.monitor_type_list[0];
          } else {
            Message({message: '获取数据失败！', type: 'error'});
          }
        },
        async FnGetMonitorData (item, data) {
          let reqData = {
              type: item.value,
              filter: {
                bmsId: this.currentBms.id
              }
            }
            if (item.value !== 'disk_used') {
              reqData.timeGranularity = this.sel_time; // 时间颗粒度
            }
            if (item.value === 'disk_throughput' || item.value === 'disk_iops' || item.value === 'network_throughput') {
              reqData.device = data.default.id; // 硬盘id或网卡id
            }
            if (this.monitor_type_default.type === 'device') { // 设备监控
              reqData.groupbyParams = item.value === 'cpu_temperature' ? 'cpuId' : 'fanId';
            }
            this.$set(item, 'loading_stauts', true);
            let res = await getMonitorData(reqData);
            if (res.code === 'Success') {
              let Data = res.data;
              this.$set(item, 'loading_stauts', false);
              if (item.value !== 'disk_used') {
                if (Data.timestamp.length > 0) {
                  this.$set(item, 'nomoreData_stauts', false);
                  this.setOptionData(Data, item.value)
                } else {
                  this.$set(item, 'nomoreData_stauts', true);
                }
              } else {
                if (Data.length > 0) {
                  this.diskUsedData = Data;
                  this.$set(item, 'nomoreData_stauts', false);
                } else {
                  this.$set(item, 'nomoreData_stauts', true);
                }
              }
            } else {
              this.$set(item, 'loading_stauts', false);
              this.$set(item, 'nomoreData_stauts', true);
              Message({message: '获取数据失败！', type: 'error'});
            }
        },
        setOptionData (Data, type) {
          if (!Data) return
          this.$nextTick(() => {
            let myChart = this.$echarts.init(document.getElementById(`${type}`));
            let {unit, date, seriesData, schemeData} = this.FnFormatData(Data, type);
            var self = this
            var option = {
              tooltip: {
                trigger: 'axis',
                backgroundColor: 'rgba(0,0,0,0.8)',
                borderColor: '#f50',
                borderWidth: 2,
                padding: 10,
                formatter: function (params) {
                  var res = params[0].name
                  for (var i = 0, l = params.length; i < l; i++) {
                    res += '<br/>' + params[i].seriesName + ' : ' + params[i].value
                  }
                  return res
                }
              },
              legend: {
                orient: 'vertical',
                x: 'right',
                y: 'center',
                data: schemeData
              },
              grid: {
                top: '7%',
                left: '6%',
                right: '14%',
                bottom: '6%',
                containLabel: true,
                show: true
              },
              toolbox: {
                left: 'center',
                feature: {
                  dataZoom: {
                    show: true,
                    title: {
                      zoom: '区域缩放',
                      back: '区域缩放-后退'
                    },
                    yAxisIndex: 'none',
                    iconStyle: {
                      normal: {
                        borderColor: '#333'
                      },
                      emphasis: {
                        borderColor: '#4984E7'
                      }
                    }
                  },
                  restore: {
                    title: '还原',
                    iconStyle: {
                      normal: {
                        borderColor: '#333'
                      },
                      emphasis: {
                        borderColor: '#4984E7'
                      }
                    }
                  },
                  saveAsImage: {
                    name: '公网流量图',
                    title: '保存为图片',
                    iconStyle: {
                      normal: {
                        borderColor: '#333'
                      },
                      emphasis: {
                        borderColor: '#4984E7'
                      }
                    }
                  }
                }
              },
              dataZoom: [{
                handleSize: 8
              }, {
                type: 'inside'
              }],
              calculable: true,
              xAxis: [
                {
                  type: 'category',
                  boundaryGap: false,
                  data: date.map(function (str) {
                    return str.replace(' ', '\n')
                  }),
                  axisLabel: {
                    formatter: function (value) {
                      var xd = new Date(value.replace('-', '/').replace('-', '/'))
                      var h = self.getStrNum(xd.getHours())
                      var m = self.getStrNum(xd.getMinutes())
                      var M = self.getStrNum(xd.getMonth() + 1)
                      var d = self.getStrNum(xd.getDate())
                      return h + ':' + m + '\n' + M + '/' + d
                    }
                  }
                }
              ],
              yAxis: [
                {
                  name: '(' + unit + ')',
                  type: 'value',
                  splitNumber: 10,
                  axisLabel: {
                    show: true
                  },
                  splitLine: {
                    lineStyle: {
                      type: 'dashed'
                    }
                  }
                }
              ],
              series: []
            }

            option.series = seriesData
            myChart.setOption(option)
          })
        },
        FnFormatData (data, type) {
          let date = []
          let keys = Object.keys(data)
          let seriesData = []
          let schemeData = []
          let unit = 'Mbps' // 吞吐量
          let model = { // 上行
            name: '',
            type: 'line',
            label: {
              normal: {
                show: false,
                position: 'top'
              }
            },
            smooth: true,
            smoothMonotone: 'x',
            data: []
          }
          if (this.monitor_type_default.type === 'source') {
            keys.forEach(item => {
              if (item === 'timestamp') {
                date = data[item]
              } else if (item === 'bms_disk_bytes_read_info' || item === 'bms_network_receive_bytes_info') {
                model.name = item === 'bms_disk_bytes_read_info' ? '读' : '入网流量'
                model.data = data[item]
                seriesData.push(JSON.parse(JSON.stringify(model)))
                schemeData.push({name: model.name})
              } else {
                switch (type) {
                  case 'cpu_used':
                    model.name = 'cpu使用率'
                    unit = '%'
                    break
                  case 'disk_used':
                    model.name = '磁盘使用率'
                    unit = '%'
                    break
                  case 'mem_used':
                    model.name = '内存使用率'
                    unit = '%'
                    break
                  case 'disk_iops':
                    model.name = 'IOPS'
                    unit = 'IOps'
                    break
                  case 'disk_throughput':
                    model.name = '写'
                    break
                  case 'network_throughput':
                    model.name = '出网流量'
                    break
                  default:
                    break
                }
                model.data = data[item]
                seriesData.push(JSON.parse(JSON.stringify(model)))
                schemeData.push({name: model.name})
              }
            })
          } else {
            let name = ''
            unit = type === 'cpu_temperature' ? '摄氏度' : 'RPM'
            keys.forEach((item, i) => {
              if (item === 'timestamp') {
                date = data[item]
              } else {
                if (type === 'cpu_temperature') {
                  name = 'cpu' + item.replace(/[^\d]/g, '')
                } else {
                  let lastStr = item.lastIndexOf('_')
                  name = '风扇' + item.substr(lastStr, item.length - 1)
                }
                model.name = name
                model.data = data[item]
                seriesData.push(JSON.parse(JSON.stringify(model)))
                schemeData.push({name: model.name})
              }
            })
          }
          return {unit, date, seriesData, schemeData}
        },
        getStrNum (n) {
          if (n < 10) return '0' + n
          return n
        }
    },
    mounted () {
        this.FnGetAppList()
        let self = this;
        window.onresize = function () {
          let myChart = null;
          self.monitor_type_default && self.monitor_type_default.tab_list.forEach(item => {
              item.list.forEach(m => {
                  if (m.value !== 'disk_used') {
                    let myChart = self.$echarts.init(document.getElementById(`${m.value}`));
                    myChart.resize();   
                  }
              })
          })
        }
    },
    watch: {
      monitor_type_default (newVal, oldVal) {
        if (newVal === oldVal) {
          return;
        }
        if (newVal) {
          if (this.sel_time !== this.time_list[0].value) {
            this.sel_time = this.time_list[0].value;
          } else {
            this.monitor_type_default.tab_list.forEach(item => {
              item.list.forEach(m => {
                this.$set(m, 'loading_stauts', true);
                this.FnGetMonitorData(m, item)
              })
            })
          }
        }
      },
      sel_time (newVal, oldVal) {
        if (newVal === oldVal) {
          return;
        }
        this.monitor_type_default.tab_list.forEach(item => {
          item.list.forEach(m => {
            this.$set(m, 'loading_stauts', true);
            this.FnGetMonitorData(m, item)
          })
        })
      },
      currentBms (newVal, oldVal) {
        if (newVal === oldVal) {
          return
        }
        this.searchStatus = false;
        console.log(111, this.searchStatus)
      }
    }
}
</script>

<style>

</style>
